module.exports = "/* Button used to open the contact form - fixed at the bottom of the page */\n.open-button {\n    background-color: #555;\n    color: white;\n    padding: 16px 20px;\n    border: none;\n    cursor: pointer;\n    opacity: 0.8;\n    position: fixed;\n    top: 50%;\n    left: 50%;\n    margin-top: -50px;\n    margin-left: -100px;\n}\n/* The popup form - hidden by default */\n.form-popup {\n    display: none;\n    position: fixed;\n    top: 10%;\n    left: 50%;\n    margin-top: -50px;\n    margin-left: -100px;\n}\n/* Add styles to the form container */\n.form-container {\n    max-width: 300px;\n    padding: 10px;\n    background-color: #d5d5d5;\n}\n/* Full-width input fields */\n.form-container input[type=text], .form-container input[type=password] {\n    width: 100%;\n    padding: 15px;\n    margin: 5px 0 22px 0;\n    border: none;\n    background: #ffffff;\n}\n/* When the inputs get focus, do something */\n.form-container input[type=text]:focus, .form-container input[type=password]:focus {\n    background-color: #ffffff;\n    outline: none;\n}\n/* Set a style for the submit/login button */\n.form-container .btn {\n    background-color: #4CAF50;\n    color: white;\n    padding: 16px 20px;\n    border: none;\n    cursor: pointer;\n    width: 100%;\n    margin-bottom:10px;\n    opacity: 0.8;\n}\n/* Add a red background color to the cancel button */\n.form-container .cancel {\n    background-color: red;\n}\n/* Add some hover effects to buttons */\n.form-container .btn:hover, .open-button:hover {\n    opacity: 1;\n}\n.hiddenCancelButton {\n    display: none;\n    background-color: red;\n    color: white;\n}\n.cardImgContainer {\n    position: relative;\n    text-align: center;\n}\n.bottom-left {\n    text-shadow:\n            -1px -1px 0 #000,\n            1px -1px 0 #000,\n            -1px 1px 0 #000,\n            1px 1px 0 #000;\n    position: absolute;\n    bottom: 8px;\n    left: 16px;\n    color: red;\n}\n.bottom-right {\n    text-shadow:\n            -1px -1px 0 #000,\n            1px -1px 0 #000,\n            -1px 1px 0 #000,\n            1px 1px 0 #000;\n    position: absolute;\n    bottom: 8px;\n    right: 16px;\n    color: green;\n}\n.top-right {\n    text-shadow:\n            -1px -1px 0 #000,\n            1px -1px 0 #000,\n            -1px 1px 0 #000,\n            1px 1px 0 #000;\n    position: absolute;\n    top: 8px;\n    right: 16px;\n    color: dodgerblue;\n}\n.card {\n     margin-left: 1vw;\n     display:inline-block;\n     height: 12vh;\n     vertical-align: top;\n}\n.targetDetails {\n    margin-left: 1vw;\n    display:inline-block;\n    height: 12vh;\n    vertical-align: top;\n    position: relative;\n}\n.card:hover {\n    box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2);\n}\n.contCard {\n    word-break: break-all;\n    font-size: 1vw;\n    padding-right: 0;\n    padding-left: 0;\n    margin-right: auto;\n    margin-left: auto;\n}\n/* Clearfix (clear floats) */\n.row{\n    margin: 0 auto;\n    text-align: center;\n    height: 14vh;\n}\n#theBoard{\n    display: none;\n    height: 84vh;\n}\n.taunt {\n    margin-left: 1vw;\n    border-top: solid #00ff24;\n    height: 12vh;\n}\n.canAttack {\n    margin-left: 1vw;\n    border-right: solid #ff0000;\n    height: 12vh;\n}\n.lifeSteal {\n    margin-left: 1vw;\n    border-left: solid #ffef00;\n    height: 12vh;\n}\n.heropower {\n    height: 8vh;\n    vertical-align: middle;\n}\n\n\n/*# sourceMappingURL=data:application/json;base64, */"